import {
  CreateLeadDTO,
  CreateTaskDTO,
  CreateWorkflowDTO,
  DashboardStatsDTO,
  Lead,
  Task,
  Workflow,
} from "@everlast/types";

const API_URL = "http://localhost:3000";
const TENANT_ID = "cmi6auqhe0000z90fahugcswd"; // Deine ID aus dem Seed (später dynamisch machen!)

async function fetchApi<T>(path: string, options?: RequestInit): Promise<T> {
  const res = await fetch(`${API_URL}${path}`, {
    ...options,
    headers: {
      "Content-Type": "application/json",
      "x-tenant-id": TENANT_ID,
      ...options?.headers,
    },
  });

  if (!res.ok) {
    throw new Error(`API Error: ${res.statusText}`);
  }

  return res.json();
}

export const apiClient = {
  // Dashboard
  getDashboardStats: () => fetchApi<DashboardStatsDTO>("/dashboard"),

  // Leads
  getLeads: () => fetchApi<Lead[]>("/leads"),
  getLead: (id: string) => fetchApi<Lead>(`/leads/${id}`),
  createLead: (data: CreateLeadDTO) =>
    fetchApi<Lead>("/leads", {
      method: "POST",
      body: JSON.stringify(data),
    }),
  updateLeadStatus: (id: string, status: string) =>
    fetchApi<Lead>(`/leads/${id}`, {
      method: "PATCH",
      body: JSON.stringify({ status }),
    }),

  // Tasks
  getTasks: (leadId?: string) => {
    const query = leadId ? `?leadId=${leadId}` : "";
    return fetchApi<Task[]>(`/tasks${query}`);
  },
  createTask: (data: CreateTaskDTO) =>
    fetchApi<Task>("/tasks", {
      method: "POST",
      body: JSON.stringify(data),
    }),
  updateTask: (id: string, data: Partial<Task>) =>
    fetchApi<Task>(`/tasks/${id}`, {
      method: "PATCH",
      body: JSON.stringify(data),
    }),

  // Workflows
  getWorkflows: () => fetchApi<Workflow[]>("/workflows"),
  createWorkflow: (data: CreateWorkflowDTO) =>
    fetchApi<Workflow>("/workflows", {
      method: "POST",
      body: JSON.stringify(data),
    }),
};
